    align-items: center;
    padding-bottom: 1rem;
}
.footer_logo{
    font-size: 1.6rem;
    color: white;
}

.footer_list {
    flex-direction: row;
    font-size: 1.3rem;
    margin: 0;
    list-style:none;
}
.footer_list-element{
    margin:0 .5rem;
}